import React from 'react';

const DemoDescriptionText = () => (
  <div>
    <p>地図上の経路をどの順番で辿れば、出発地から到着地まで最短で行けるでしょうか？</p>
    <p>辿る順番に地図上の経路のマーカーをクリックしてください。</p>
    <p>正解を確認するには、以下のボタンをクリックしてください。</p>
  </div>
);

export default DemoDescriptionText;



